
    import {defineComponent, reactive, toRefs, onMounted} from 'vue';
    import {TableFun, Table} from './tableTs/Table'

    export default defineComponent({
        name: "Table",
        setup: function () {
            let data = reactive({
                tableData: [],
                searchName: "",
                searchAddress: "",
                ...Table
            })
            onMounted(() => {
                data.tableData = TableFun.getData({page: data.page, size: data.size});
            })
            let showInfo = (row: any, index: number) => {
                console.log(row, index)
            }
            let searchFun = () => {
                data.tableData = TableFun.search({page: data.page, size: data.size}, {
                    name: data.searchName,
                    address: data.searchAddress
                });
            }
            let reset = () => {
                data.searchName = "";
                data.searchAddress = "";
            }
            const handleSizeChange = (e: number) => {
                data.size = e;
                data.tableData = TableFun.getData({page: data.page, size: data.size});
            }
            const handleCurrentChange = (e: number) => {
                data.page = e;
                data.tableData = TableFun.getData({page: data.page, size: data.size});
            }

            return {
                ...toRefs(data),
                showInfo,
                searchFun,
                reset,
                handleSizeChange,
                handleCurrentChange
            }
        },
    })
